.o-question-response {
  padding: 1px;
  overflow-x: auto;
  overflow-y: visible;
}
.o-question-response:not(.balance) > *:not(script):last-child {
  margin-bottom: 0;
}
.o-question-response.balance {
  display: flex;
  flex-wrap: wrap;
}
.o-question-response.balance > .o-option-sublist {
  display: flex;
  flex-wrap: wrap;
  flex: 1 0 100%;
}
.o-question-response.balance .a-label-heading-sublist {
  flex-basis: 100%;
  width: 100%;
}

.o-question-core {
  z-index: 2;
}